@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  src: url("../assets/fonts/montserrat-regular.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  src: url("../assets/fonts/montserrat-bold.ttf") format("truetype");
}

@font-face {
  font-family: "Druk Wide";
  font-weight: 400;
  src: url("../assets/fonts/druk-wide-cyr-regular.otf") format("opentype");
}
@font-face {
  font-family: "Druk Wide";
  font-weight: 700;
  src: url("../assets/fonts/druk-wide-cyr-bold.otf") format("opentype");
}

@font-face {
  font-family: 'DisposableDroid BB';
  src: url("../assets/fonts/disposable-droid-bb.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Metanart';
  src: url("../assets/fonts/metanart.ttf") format('truetype');
  font-weight: 400;
}


* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400;
  font-family: "Montserrat", Arial, sans-serif;
  background: #ffffff;
  color: #242424;
  overflow-x: hidden;
  overflow-y: auto;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font-family: inherit;
}

h1 {
  margin: 0;
  font-family: "Druk Wide";
  font-size: 32px;
  line-height: 48px;
  font-weight: 700;
}

h2 {
  margin: 0;
  font-family: "Druk Wide";
  font-size: 24px;
  line-height: 31px;
  font-weight: bold;
}

.flex {
  display: flex;
  align-items: center;
}

.bubble {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  padding: 0 0.5em;
  margin: 0 0.1em 0.1em 0;
  background: #fde033;
  border-radius: 40px;
}

.divider {
  width: 85%;
  margin: 70px auto;
  opacity: 0.2;
  border: 5px solid #5ea9c1;
  border-radius: 5px;
}

a.black-btn {
  display: inline-block;
  width: fit-content;
  padding: 0.35em 0.8em 0.4em;
  margin-top: 0.4em;
  text-decoration: none;
  color: #ffffff;
  background: #242424;
  border: 1px solid #242424;
  border-radius: 1em;
}
a.black-btn:hover {
  background: #ffffff;
  color: #242424;
}

.desktop {
  display: none;
}
.mobile {
  display: block;
}

header, main, footer {
  max-width: 1440px;
  margin: 0 auto;
}

header {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  justify-content: space-between;
  padding: 24px 20px;
}
header .logo {
  color: #242424;
  text-decoration: none;
}
header .logo,
header .up-btn {
  font-weight: 700;
}
header .up-btn {
  display: none;
}
header .tags {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 99;
  right: 20px;
  top: 20px;
  padding: 4px 8px;
  background: #ffffff;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.15);
  border-radius: 20px;

  max-height: 30px;
  transition: max-height 100ms ease-out;
  overflow: hidden;
}
header .tags.opened {
  max-height: 300px;
  transition: max-height 100ms ease-in;
}
header .tags b {
  text-align: center;
  padding-bottom: 6px;
  border-radius: 20px;
}
header .tags button {
  padding: 4px 12px 6px 12px;
  background: #ffffff;
  border-radius: 20px;
  margin: 0px 2px;
}
header .tags button.active {
  background: #242424;
  color: #ffffff;
}

main {
  position: relative;
  width: 100%;
  padding: 24px 20px 158px 20px;
}
main .main-header {
  display: block;
  margin-top: 5em;
}
main .main-header h1 {
  margin-right: 20px;
}
main .main-header .flex .bubble:first-child {
  width: 48px;
}
main .simple-text {
  width: 100%;
  max-width: 400px;
  margin: 60px 0 40px 0;
}
main .simple-text div {
  margin-bottom: 1em;
}

main .shorts-block {
  display: flex;
  flex-direction: column;
  margin: 70px 0;
  padding: 30px 0;
  border-top: 2px solid #242424;
  border-bottom: 2px solid #242424;
}
main .shorts-block h2 {
  width: 100%;
  margin-bottom: 10px;
}
main .shorts-block div {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin-top: 30px;
}
main .shorts-block b {
  margin-bottom: 8px;
}
main .shorts-block a {
  margin-top: 8px;
}

footer {
  width: 100%;
  padding: 24px 20px;
  justify-content: space-between;
  flex-wrap: wrap;
}
footer .socials {
  width: 100%;
  max-width: 340px;
  justify-content: space-between;
  margin-bottom: 1.5em;
}
footer .socials a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  margin: 0 0.1em 0.1em 0;
  border-radius: 50%;
  background: #242424;
  border: 3px solid #242424;
}
footer .socials a img {
  width: 60%;
  object-fit: contain;
}
footer .send-link {
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 400px;
  padding: 0 2em;
  margin-bottom: 1.3em;
  background: #242424;
  border: 3px solid #242424;
  color: #ffffff;
  border-radius: 3em;
  font-family: "Druk Wide";
  font-size: 1.15em;
  line-height: 1.3em;
  text-decoration: none;
}
footer .socials a:hover,
footer .send-link:hover {
  background: #ffffff;
  color: #242424;
}
footer .socials a:hover img {
  filter: invert(1);
}

@media screen and (min-width: 768px) {
  main .shorts-block {
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 1020px;
    margin-left: auto;
  }
  main .shorts-block div {
    max-width: 240px;
    margin-right: 100px;
  }

  footer .socials {
    max-width: 500px;
  }
  footer .socials a {
    width: 80px;
    height: 80px;
  }
}

@media screen and (min-width: 1024px) {
  h1 {
    font-size: 40px;
  }

  .desktop {
    display: block;
  }
  .mobile {
    display: none;
  }

  header {
    padding: 20px 40px;
  }
  header .logo,
  header .up-btn {
    font-size: 1.25em;
  }
  header .up-btn {
    display: block;
  }
  header .tags {
    position: relative;
    top: unset;
    right: unset;
    flex-direction: row;
    border-radius: 40px;
  }
  header .tags b {
    display: none;
  }

  main {
    padding: 24px 40px 168px 40px;
  }
  main .main-header {
    display: flex;
  }
  main .simple-text a.desktop {
    display: flex;
  }

  footer {
    padding: 20px 40px;
  }
}

@media screen and (min-width: 1280px) {
  footer .socials {
    max-width: 600px;
  }
  footer .socials a {
    width: 100px;
    height: 100px;
  }
  footer .send-link {
    height: 100px;
    max-width: fit-content;
    font-size: 1.3em;
  }
}
