@font-face {
  font-family: Montserrat;
  font-weight: 400;
  src: url("montserrat-regular.1c26291d.ttf") format("truetype");
}

@font-face {
  font-family: Montserrat;
  font-weight: 700;
  src: url("montserrat-bold.b170a58f.ttf") format("truetype");
}

@font-face {
  font-family: Druk Wide;
  font-weight: 400;
  src: url("druk-wide-cyr-regular.af627d0a.otf") format("opentype");
}

@font-face {
  font-family: Druk Wide;
  font-weight: 700;
  src: url("druk-wide-cyr-bold.d501efe7.otf") format("opentype");
}

@font-face {
  font-family: DisposableDroid BB;
  src: url("disposable-droid-bb.1fb6e286.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Metanart;
  src: url("metanart.ada9136b.ttf") format("truetype");
  font-weight: 400;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
}

html, body {
  width: 100%;
  height: 100%;
  color: #242424;
  background: #fff;
  margin: 0;
  padding: 0;
  font-family: Montserrat, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  overflow-x: hidden;
  overflow-y: auto;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
}

h1 {
  margin: 0;
  font-family: Druk Wide;
  font-size: 32px;
  font-weight: 700;
  line-height: 48px;
}

h2 {
  margin: 0;
  font-family: Druk Wide;
  font-size: 24px;
  font-weight: bold;
  line-height: 31px;
}

.flex {
  align-items: center;
  display: flex;
}

.bubble {
  height: 48px;
  background: #fde033;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  margin: 0 .1em .1em 0;
  padding: 0 .5em;
  display: flex;
}

.divider {
  width: 85%;
  opacity: .2;
  border: 5px solid #5ea9c1;
  border-radius: 5px;
  margin: 70px auto;
}

a.black-btn {
  width: fit-content;
  color: #fff;
  background: #242424;
  border: 1px solid #242424;
  border-radius: 1em;
  margin-top: .4em;
  padding: .35em .8em .4em;
  text-decoration: none;
  display: inline-block;
}

a.black-btn:hover {
  color: #242424;
  background: #fff;
}

.desktop {
  display: none;
}

.mobile {
  display: block;
}

header, main, footer {
  max-width: 1440px;
  margin: 0 auto;
}

header {
  z-index: 99;
  width: 100%;
  justify-content: space-between;
  padding: 24px 20px;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

header .logo {
  color: #242424;
  text-decoration: none;
}

header .logo, header .up-btn {
  font-weight: 700;
}

header .up-btn {
  display: none;
}

header .tags {
  z-index: 99;
  max-height: 30px;
  background: #fff;
  border-radius: 20px;
  flex-direction: column;
  padding: 4px 8px;
  transition: max-height .1s ease-out;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 24px #00000026;
}

header .tags.opened {
  max-height: 300px;
  transition: max-height .1s ease-in;
}

header .tags b {
  text-align: center;
  border-radius: 20px;
  padding-bottom: 6px;
}

header .tags button {
  background: #fff;
  border-radius: 20px;
  margin: 0 2px;
  padding: 4px 12px 6px;
}

header .tags button.active {
  color: #fff;
  background: #242424;
}

main {
  width: 100%;
  padding: 24px 20px 158px;
  position: relative;
}

main .main-header {
  margin-top: 5em;
  display: block;
}

main .main-header h1 {
  margin-right: 20px;
}

main .main-header .flex .bubble:first-child {
  width: 48px;
}

main .simple-text {
  width: 100%;
  max-width: 400px;
  margin: 60px 0 40px;
}

main .simple-text div {
  margin-bottom: 1em;
}

main .shorts-block {
  border-top: 2px solid #242424;
  border-bottom: 2px solid #242424;
  flex-direction: column;
  margin: 70px 0;
  padding: 30px 0;
  display: flex;
}

main .shorts-block h2 {
  width: 100%;
  margin-bottom: 10px;
}

main .shorts-block div {
  max-width: 400px;
  flex-direction: column;
  margin-top: 30px;
  display: flex;
}

main .shorts-block b {
  margin-bottom: 8px;
}

main .shorts-block a {
  margin-top: 8px;
}

footer {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 24px 20px;
}

footer .socials {
  width: 100%;
  max-width: 340px;
  justify-content: space-between;
  margin-bottom: 1.5em;
}

footer .socials a {
  width: 50px;
  height: 50px;
  background: #242424;
  border: 3px solid #242424;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 .1em .1em 0;
  display: flex;
}

footer .socials a img {
  width: 60%;
  object-fit: contain;
}

footer .send-link {
  width: 100%;
  height: 80px;
  max-width: 400px;
  color: #fff;
  background: #242424;
  border: 3px solid #242424;
  border-radius: 3em;
  align-items: center;
  margin-bottom: 1.3em;
  padding: 0 2em;
  font-family: Druk Wide;
  font-size: 1.15em;
  line-height: 1.3em;
  text-decoration: none;
  display: flex;
}

footer .socials a:hover, footer .send-link:hover {
  color: #242424;
  background: #fff;
}

footer .socials a:hover img {
  filter: invert();
}

@media screen and (min-width: 768px) {
  main .shorts-block {
    max-width: 1020px;
    flex-flow: wrap;
    margin-left: auto;
  }

  main .shorts-block div {
    max-width: 240px;
    margin-right: 100px;
  }

  footer .socials {
    max-width: 500px;
  }

  footer .socials a {
    width: 80px;
    height: 80px;
  }
}

@media screen and (min-width: 1024px) {
  h1 {
    font-size: 40px;
  }

  .desktop {
    display: block;
  }

  .mobile {
    display: none;
  }

  header {
    padding: 20px 40px;
  }

  header .logo, header .up-btn {
    font-size: 1.25em;
  }

  header .up-btn {
    display: block;
  }

  header .tags {
    top: unset;
    right: unset;
    border-radius: 40px;
    flex-direction: row;
    position: relative;
  }

  header .tags b {
    display: none;
  }

  main {
    padding: 24px 40px 168px;
  }

  main .main-header, main .simple-text a.desktop {
    display: flex;
  }

  footer {
    padding: 20px 40px;
  }
}

@media screen and (min-width: 1280px) {
  footer .socials {
    max-width: 600px;
  }

  footer .socials a {
    width: 100px;
    height: 100px;
  }

  footer .send-link {
    height: 100px;
    max-width: fit-content;
    font-size: 1.3em;
  }
}

